import Router from './Router';
import { useEffect, useState, useRef } from 'react';

// modal import
import ReactModal from 'react-modal';
import ModalsProvider from './modal/ModalsProvider';
ReactModal.setAppElement('#root');

function App() {
	const [loading, setLoading] = useState(true);
	const loader = useRef();
	const opening = () => {
		const isTouchDevice = 'ontouchstart' in window || navigator.maxTouchPoints > 0 || navigator.msMaxTouchPoints > 0;
		if (!isTouchDevice) {
			document.body.classList.add('pc');
		}
		setLoading(false);
		loader.current.style.animation = 'fade-out 1s ease-in-out 3.5s 1 forwards';
		setTimeout(() => {
			loader.current.remove();
		}, 4500);
	};
	useEffect(() => {
		window.addEventListener('load', opening);
		return () => {
			window.removeEventListener('load', opening);
		};
	}, [loading]);

	return (
		<>
			<div className={`loading ${loading ? '' : 'active'}`} ref={loader}>
				{loading ? (
					<div className="spinner h4">Loading</div>
				) : (
					<svg width="225" height="96" viewBox="0 0 225 96">
						{'봉쥬르펜션'.split('').map((letter, i) => (
							<text key={i} x={i * 45} y="65">
								{letter}
							</text>
						))}
					</svg>
				)}
			</div>
			<ModalsProvider>
				<div id="wrap">
					<Router />
				</div>
			</ModalsProvider>
		</>
	);
}

export default App;
