import useModals from '../components/hooks/useModals';
import { modals } from '../components/modal/Modals';

import { roomData } from '../assets/js/Data';

function Rooms() {
	const { openModal } = useModals();
	const handleClick = (e) => {
		openModal(modals.Room, {
			dataId: Number(e.currentTarget.dataset.id),
		});
	};

	return (
		<div className="content rooms">
			{roomData.map((room) => (
				<div className="detail" key={room.id}>
					<h2 className="title">
						{room.en}
						<span className="h3"> {room.ko}</span>
					</h2>
					<button onClick={handleClick} data-id={room.id}>
						<figure className="roomData-figure">
							<img src={`${process.env.PUBLIC_URL}/img/room/${room.en.charAt(0)}0.jpg`} alt="" />
							<img src={`${process.env.PUBLIC_URL}/img/room/${room.en.charAt(0)}1.jpg`} alt="" />
							<figcaption>{room.ko} 객실 내부의 모습</figcaption>
						</figure>
						<div className="cont">
							<p>{room.personnel}</p>
							<p>{room.config}</p>
							<p>{room.size}</p>
						</div>
					</button>
				</div>
			))}
			<div className="map">
				<h2 className="title">객실배치도</h2>
				<h3 className="sub-title">배치도 내 객실 이름을 클릭하시면 정보를 확인하실 수 있습니다.</h3>
				<ul className="info-list">
					<li className="info">
						<p className="info-title">
							<strong className="sky">SKY</strong> 2인 기준 최대 6인
						</p>
						<div className="info-content">
							{Array.from({ length: 4 }, (v, i) => i).map((list) => (
								<button onClick={handleClick} key={list + 1} data-id="2">
									<i className={`s${list + 1}`}>S{list + 1}</i> <span>SKY{list + 1}</span>
								</button>
							))}
						</div>
					</li>
					<li className="info">
						<p className="info-title">
							<strong className="cloud">CLOUD</strong> 2인 기준 최대 3인
						</p>
						<div className="info-content">
							{Array.from({ length: 4 }, (v, i) => i).map((list) => (
								<button onClick={handleClick} key={list + 1} data-id="3">
									<i className={`c${list + 1}`}>C{list + 1}</i> <span>CLOUD{list + 1}</span>
								</button>
							))}
						</div>
					</li>
					<li className="info">
						<p className="info-title">
							<strong className="vally">VALLY</strong> 4인 기준 최대 8인
						</p>
						<div className="info-content">
							{Array.from({ length: 2 }, (v, i) => i).map((list) => (
								<button onClick={handleClick} key={list + 1} data-id="0">
									<i className={`v${list + 1}`}>V{list + 1}</i> <span>VALLY{list + 1}</span>
								</button>
							))}
						</div>
					</li>
					<li className="info">
						<p className="info-title">
							<strong className="mountain">MOUNTAIN</strong> 2인 기준 최대 6인
						</p>
						<div className="info-content">
							<button onClick={handleClick} data-id="1">
								<i className="m">M</i> <span>MOUNTAIN</span>
							</button>
						</div>
					</li>
					<li className="info">
						<p className="info-title">그 외 시설</p>
						<div className="info-content">
							<div>
								<i className="ex">P1</i> <span>제 1 주차장</span>
							</div>
							<div>
								<i className="ex">P2</i> <span>제 2 주차장</span>
							</div>
							<div>
								<i className="ex">P3</i> <span>제 3 주차장</span>
							</div>
							<div>
								<i className="ex">O</i> <span>관리사무실</span>
							</div>
							<div>
								<i className="ex">T</i> <span>분리수거장</span>
							</div>
						</div>
					</li>
				</ul>
				<figure className="map-figure">
					<i className="img"></i>
					<figcaption>봉쥬르펜션의 객실배치도</figcaption>
					<div className="btn-modal">
						{Array.from({ length: 2 }, (v, i) => i).map((list) => (
							<button className={`v${list + 1}`} key={list + 1} onClick={handleClick} data-id="0">
								V{list + 1}
							</button>
						))}
						<button className="m" onClick={handleClick} data-id="1">
							M
						</button>
						{Array.from({ length: 4 }, (v, i) => i).map((list) => (
							<button className={`s${list + 1}`} key={list + 1} onClick={handleClick} data-id="2">
								S{list + 1}
							</button>
						))}
						{Array.from({ length: 4 }, (v, i) => i).map((list) => (
							<button className={`c${list + 1}`} key={list + 1} onClick={handleClick} data-id="3">
								C{list + 1}
							</button>
						))}
						<div className="ex p1">P1</div>
						<div className="ex p2">P2</div>
						<div className="ex p3">P3</div>
						<div className="ex o">O</div>
						<div className="ex t">T</div>
					</div>
				</figure>
			</div>
		</div>
	);
}

export default Rooms;
